export function addEgg(){
    const eggArea = document.getElementById('egg-area');
    let eggLimit = 5;

    if (eggArea.childElementCount < eggLimit){
        const imageContainer = document.createElement('img');

        imageContainer.setAttribute('class', 'egg-image');
        imageContainer.src = "https://res.cloudinary.com/dzmhkee5i/image/upload/v1727227065/fuzzy/ozea2mvwcncfzvwvebku.png";

        eggArea.appendChild(imageContainer);
    }else {
        window.alert('Reached Limit')
    }
}

export function removeEgg(){
    const eggArea = document.getElementById('egg-area');
    if (eggArea.childElementCount == 0){
        window.alert('The area is empty!');
    }else {
        const lastChild = eggArea.lastElementChild;
        eggArea.removeChild(lastChild);
    }
}

export function windColor(tempValue){
    if (tempValue < 16) {
        return "blue"; // Out of range for defined categories
    } else if (tempValue < 30) {
        return "blue"; // Full blue for Cold Temp
    } else if (tempValue < 34) {
        // Gradual transition from blue to light blue
        const ratio = (tempValue - 30) / (34 - 30); // From 0 to 1
        return `rgba(173, 216, 230, ${ratio})`; // lightblue with transparency based on ratio
    } else if (tempValue < 38) {
        return "lightblue"; // Full light blue for Low Temp
    } else if (tempValue < 42) {
        // Gradual transition from light blue to green
        const ratio = (tempValue - 38) / (42 - 38); // From 0 to 1
        return `rgba(144, 238, 144, ${ratio})`; // green with transparency based on ratio
    } else if (tempValue < 50) {
        return "green"; // Full green for High Temp
    } else if (tempValue < 50) {
        return "orange"; // Full orange for Critical Temp
    } else {
        return "red"; // Critical Temp and above
    }
    // if (tempValue < 30) {
    //     return "blue";
    // } else if (tempValue < 34) {
    //     return "lightblue";
    // } else if (tempValue < 38) {
    //     return "green";
    // } else if (tempValue < 50) {
    //     return "orange";
    // } else {
    //     return "red";
    // }
}